import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import '../index.css'


const HookUseState = () => {
    const [count, setCount] = useState(10);

    if (count < 0) {
        alert("Value Cannot Be Negative!");
        setCount(0);
    }
    else {
        return (
            <div className="Hookuse">
                <h3 className="HeadingOfHook">Value of Counter: {count}</h3><br/>
                <Button variant="outline-dark" onClick={() => setCount(count + 1)} block>Increse Counter Value</Button><br/>
                <Button variant="outline-info" onClick={() => setCount(count - 1)} block>Decrese Counter Value</Button><br/>
                <Button  variant="outline-dark" onClick={() => setCount(0)} block>Set to 0</Button>
            </div>
        );
    }
};

export default HookUseState;